.aibp-status {
  display: inline-block;
  vertical-align: middle;
  padding-left: 16px;
  position: relative;
}
.aibp-status:before {
  position: absolute;
  left: 0;
  top: 50%;
  content: "";
  width: 8px;
  height: 8px;
  border-radius: 4px;
  background-color: #999;
  transform: translateY(-50%);
}
.aibp-status.normal:before {
  background-color: #5FB333;
}
.aibp-status.error:before {
  background-color: #d0021b;
}
.aibp-status.holding:before {
  background-color: #108cee;
}
.aibp-status.warning:before {
  background-color: #F39000;
}
.aibp-status.unavailable:before {
  background-color: #666;
}
.aibp-status.loading:before {
  background-color: #108cee;
  left: -10px;
  -webkit-animation: animation-before 1.5s;
          animation: animation-before 1.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.aibp-status.loading:after {
  position: absolute;
  content: "";
  top: 50%;
  width: 8px;
  height: 8px;
  border-radius: 4px;
  background-color: #F39000;
  left: 0;
  -webkit-animation: animation-after 1.5s;
          animation: animation-after 1.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  transform: translateY(-50%);
}
@-webkit-keyframes animation-before {
  0% {
    left: -10px;
  }
  50% {
    left: 0;
  }
  100% {
    left: -10px;
  }
}
@keyframes animation-before {
  0% {
    left: -10px;
  }
  50% {
    left: 0;
  }
  100% {
    left: -10px;
  }
}
@-webkit-keyframes animation-after {
  0% {
    left: 0;
  }
  50% {
    left: -10px;
  }
  100% {
    left: 0;
  }
}
@keyframes animation-after {
  0% {
    left: 0;
  }
  50% {
    left: -10px;
  }
  100% {
    left: 0;
  }
