.color-block {
  position: relative;
  width: 60Px;
  border-radius: 6Px;
  height: 28Px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 8Px;
  cursor: pointer;
}
.color-block:after {
  position: absolute;
  top: 10Px;
  left: 0;
  height: 100%;
  width: 100%;
  content: "Copied!";
  font-size: 12Px;
  line-height: 28Px;
  text-align: center;
  color: #444;
